import axios from "axios";
import React, { useState } from "react";

interface TaskListAutogeneratedProps {}

export function TaskListAutogenerated(props: TaskListAutogeneratedProps) {
  const [list_task, setListTask] = useState<string[]>([]);
  const [loading, setLoading] = useState<boolean>(false);
  const [message, setMessage] = useState<string>('Maintain zero data breach incidents');

  async function send() {
    if (!message.trim() || loading) {
      return;
    }
    setLoading(true);
    try {
      const res_chat = await axios.post(`/ask-task-recommendation`, { message }, {
        baseURL: 'http://localhost:5008',
        headers: { authorization: 'Bearer eyJhbGciOiJIUzI1NiJ9.Mjc.OfZrterxyfM9JWL1jhnLk7UnxaJdk4-X_p_HxK6xY1c' },
        responseType: 'stream',
        adapter: 'fetch',
      });

      const decoder = new TextDecoder();
      let result = '';
      for await (const chunk of res_chat.data) {
        const chunk_message = decoder.decode(chunk);
        result += String(chunk_message);
        setListTask(result.split(/\n+/).map(x => x.trim()));
      }
      setLoading(false);
    } catch (err: any) {
      console.log(err);
      setLoading(false);
    }
  }

  return (
    <div className={`flex flex-col gap-[12px]`}>
      <div>
        Objective Task Generator Sample
      </div>
      <textarea 
        className={`bg-slate-100 outline-none p-[4px_12px] rounded-[8px]`}
        placeholder={'Enter objective title'}
        value={message} 
        onChange={e => setMessage(e.target.value ?? '')}>
      </textarea>
      <div>
        List Task
      </div>
      {
        list_task.map((task, i) => (
          <div
            key={i}
            className={`flex items-center gap-2`}>
            <input type={'checkbox'} />
            <input 
              className={`p-[4px_12px] bg-gray-100 outline-none rounded-[8px] flex-1`}
              value={task}
              onChange={e => setListTask([
                ...list_task.slice(0, i),
                e.target.value,
                ...list_task.slice(i + 1),
              ])} />
          </div>
        ))
      }
      <button 
        className={`bg-slate-500 text-white p-[8px] rounded-[4px]`}
        onClick={() => send()}>
        { loading ? 'Loading...' : 'Generate' }
      </button>
    </div>
  );
}
