import React from "react";

interface GeneralTabProps {
  tabs: string[]
  active: string
  onTabChange(tab: string): void
}

export function GeneralTab(props: GeneralTabProps) {
  return (
    <div className={`flex overflow-x-auto font-[Poppins] border-b border-b-px border-b-[#EAEFF5]`}>
      { props.tabs.map((tab, i) => {
        const active = props.active == tab;

        return (
          <div 
            onClick={() => props.onTabChange(tab)}
            className={`flex flex-col flex-1 cursor-pointer`}>
            <div className={`text-[#0066B9] text-center p-[16px] ${active ? 'text-[#0066B9] font-medium' : 'text-[#8DA6C4]'}`}>
              { tab }
            </div>
            <div className={active ? 'bg-[#0066B9] h-[3px]' : 'bg-transparent h-[3px]'} />
          </div>
        );
      }) }
    </div>
  );
}
